  .mostrar{
    position: absolute;
    top: 300px;
    right: 20px;
    z-index: 1010;
    margin-top: 7px;
    background-color: rgba(243, 242, 242, 2);
    animation:pulse 1.5s infinite;
    box-shadow:0 0 0 0  rgb(105, 51, 51);
    border: hsl(hue, saturation, lightness);
    border-radius: 8px;
    cursor: pointer;
    
  }


  .mostrar {
    color: rgb(104, 96, 96);
    transition: all 0.5s;
   
  }
  .mostrar::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: rgba(238, 48, 48, 0.1);
    transition: all 0.3s;
  }
  .mostrar:hover::before {
    opacity: 0 ;
    transform: scale(0.5,0.5);
  }
  .mostrar::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0;
    transition: all 0.3s;
    border: 1px solid rgba(253, 252, 252, 2);
    transform: scale(1.2,1.2);
    
  }
  .mostrar:hover::after {
    opacity: 1;
    transform: scale(1,1);
  }
  @keyframes moveInBottom {
    0% {
        opacity: 0;
        transform: translateY(30px);
    }

    100% {
        opacity: 1;
        transform: translateY(0px);
    }}
